.header {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-pack: justify;
        justify-content: space-between;
    padding: 20px;
    background: -webkit-gradient(radial, 50% 50%, 0, 50% 50%, 930, from(#09afcc), to(#011539));
    position: fixed;
    width: 100vw;
    top: 0;
}
.name {
    font-size: 40px;
    width: 100%;
    text-align: center;
    color: white;
   font-weight: 500;
}
.header img {
    width: 50px;
    margin-right: 10px;
    cursor: pointer;
}
.links {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-align: center;
        align-items: center;
}
@media (max-width: 510px){
    .name {
        font-size: 30px;
    }
}


